import React, { Component } from "react";
import "./css/styles.css";
import { BrowserRouter as Router } from "react-router-dom";
import Index from "./partials/index";

const loader = document.querySelector(".loader");
const body = document.getElementById("body");

//const hideLoader = () => loader.classList.add("loader--hide");
//const overflowActive = () => body.style.overflow("visible");

class App extends Component {
  render() {
    if (loader && !loader.classList.contains(".loader--hide")) {
      setTimeout(() => {
        loader.classList.add("loader--fadeOut");
      }, 1000);
      setTimeout(() => {
        loader.classList.add("loader--hide");
        loader.classList.remove("loader-fadeOut");
        body.style.overflow = "visible";
      }, 2000);
    }
    return (
      <Router>
        <div className="nav">
          <div className="title">KID TRAVIS</div>
          <ul className="nav-list">
            <li className="nav-item">
              <a href="/">Home</a>
            </li>
            <li className="nav-item">
              <a href="https://fanlink.to/kidtravis">Music</a>
            </li>
            <li className="nav-item">
              <a href="/">Shop</a>
            </li>
          </ul>
        </div>
        <Index />
      </Router>
    );
  }
}

export default App;
